<template>
    
   <div class="panel-body">
        <!-- Main content -->
        <div class="content-wrapper  col-sm-12">
            <div class="card">
                <div class="row">
                    <div class="col-md-6">
                        <div v-if="loading_status" class="alert alert-success">
                        <a href="#" class="close" data-dismiss="alert" aria-label="close">&times;</a>
                        <strong>Success!</strong> {{ status }}.
                    </div>
                    <form action="#" method="post" v-on:submit.prevent="submitForm()">
                        <h4 style="margin-top:0px;">Details</h4>
                        <!-- <div v-if="pictureImage == ''" class="form-group">
                            <label><strong>Upload Image:</strong></label>
                            <div class="uniform-uploader">
                                <input type="file" id="file" ref="file" v-on:change="handleFileUpload()" class="form-input-styled form-control">
                            </div>
                        </div>
 
                        <div v-if="pictureImage != ''" class="form-group">
                            <label><strong>Upload Image:</strong></label>
                                <img class="img-responsive img-thumbnail" :src="pictureImage">
                        </div> -->

                        <div class="form-group">
                            <label><strong>Delivery Name:</strong></label>
                            <input type="text" :readonly="isReadonly" v-model="delivery_name" class="form-control" placeholder="Delivery Name">
                        </div>

                        <div class="form-group">
                            <label><strong>Plate Number:</strong></label>
                            <input type="text" :readonly="isReadonly" v-model="plate_number" class="form-control" placeholder="Plate Number">
                        </div>
                    </form>
                    </div>
                    <div class="col-md-6">
                        <h4 style="margin-top:0px;">Attachments</h4>
                        <div class="row">
                            <div class="col-md-6">
                                <div class="row">
                                    <div class="col-md-12">
                                        <div v-if="pictureInvoice == ''" class="form-group">
                                            <label><strong>Invoice:</strong></label>
                                            <div class="uniform-uploader">
                                                <input type="file" id="file" ref="invoice" v-on:change="invoiceFile" class="form-input-styled form-control">
                                            </div>
                                        </div>
                
                                        <div v-if="pictureInvoice != ''" class="form-group">
                                            <label><strong>Invoice:</strong></label>
                                                <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureInvoice_url">
                                        </div>
                                    </div>
                                    <div class="col-md-12">
                                        <div v-if="pictureRSSlip == ''" class="form-group">
                                            <label><strong>RS Slip:</strong></label>
                                            <div class="uniform-uploader">
                                                <input type="file" id="file" ref="RSSlip" v-on:change="rsslipFile" class="form-input-styled form-control">
                                            </div>
                                        </div>
                
                                        <div v-if="pictureRSSlip != ''" class="form-group">
                                            <label><strong>RS Slip:</strong></label>
                                                <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureRSSlip_url">
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="row">
                                    <div class="col-md-12">
                                        <div v-if="pictureDriversID == ''" class="form-group">
                                            <label><strong>Driver's ID:</strong></label>
                                            <div class="uniform-uploader">
                                                <input type="file" id="file" ref="driversID" v-on:change="driveridFile" class="form-input-styled form-control">
                                            </div>
                                        </div>
                
                                        <div v-if="pictureDriversID != ''" class="form-group">
                                            <label><strong>Driver's ID:</strong></label>
                                                <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureDriversID_url">
                                        </div>
                                    </div>
                                    <div class="col-md-12">
                                        <div v-if="pictureActualRS == ''" class="form-group">
                                            <label><strong>Actual RS:</strong></label>
                                            <div class="uniform-uploader">
                                                <input type="file" id="file" ref="actualRS" v-on:change="actualrsFile" class="form-input-styled form-control">
                                            </div>
                                        </div>
                
                                        <div v-if="pictureActualRS != ''" class="form-group">
                                            <label><strong>Actual RS:</strong></label>
                                                <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureActualRS_url">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="text-right">
                        <button v-on:click="redirectPrevious()" :disabled="isDisabled" type="button" class="btn btn-default" style="margin-right: 5px;">Cancel</button>
                        <button type="submit" class="btn btn-primary" @click="submitForm" :disabled="isDisabled2">Save</button>
                    </div>
                </div>
            </div>
        </div>
    </div>


</template>
<script>

</script>
<script>
import axios from 'axios'
import moment from 'moment'
import { msgError, msgSuccess, tokenApi, apiUrl, imageUrl} from './../../assets/custom/custom.js'
import { async } from 'q'

export default {
    data (){
        return {
            rs_id: '',
            rs_action: '',
            delivery_name: '',
            plate_number: '',
            file: '',
            status: '',
            pictureImage: '',
            pictureInvoice: '',
            pictureRSSlip: '',
            pictureDriversID: '',
            pictureActualRS: '',
            pictureInvoice_url: '',
            pictureRSSlip_url: '',
            pictureDriversID_url: '',
            pictureActualRS_url: '',
            loading_status: false,
            isDisabled: false,
            isDisabled2: false,
            isReadonly: false
        }
    },
    created() {
        this.rs_id = this.$route.params.rs_id
        this.rs_action = this.$route.params.rs_action
    },
    computed: {
         message(){
             return this.$store.getters.message
         }
    },
    async mounted(){
        await this.getFormDetails()
    },
    methods: {
        async handdleError(error=null) {
            if (error != null) {
                const response = JSON.parse(JSON.stringify(error))
                const status = response.response.status.toString()
                const message = (response.response.data.error === undefined) ? response.response.data.message : response.response.data.error.message
                msgError(status, message)
                return true
            }
                msgError(401, "Something Wrong")
        },
        async redirectPrevious() {
            this.$router.push({ name: 'RsInquiry' })
        },
        async invoiceFile() {
            this.pictureInvoice = this.$refs.invoice.files[0]
            this.pictureInvoice_url = URL.createObjectURL(this.pictureInvoice)
        },
        async rsslipFile() {
            this.pictureRSSlip = this.$refs.RSSlip.files[0]
            this.pictureRSSlip_url = URL.createObjectURL(this.pictureRSSlip)
        },
        async driveridFile() {
            this.pictureDriversID = this.$refs.driversID.files[0]
            this.pictureDriversID_url = URL.createObjectURL(this.pictureDriversID)
        },
        async actualrsFile() {
            this.pictureActualRS = this.$refs.actualRS.files[0]
            this.pictureActualRS_url = URL.createObjectURL(this.pictureActualRS)
        },
        async submitForm() {
            // this.isDisabled = true
            // this.isDisabled2 = true
            let formData = new FormData()
            // if (this.file == "") {
            //     return msgError(401, "Invoice file is required")
            // } else 
            if(this.delivery_name == "") {
                return msgError(401, "Delivery name is required")
            } else if(this.plate_number == "") {
                return msgError(401, "Plate number is required")
            } else if(this.pictureInvoice == "") {
                return msgError(401, "Invoice is required")
            } else if(this.pictureRSSlip == "") {
                return msgError(401, "RS Slip is required")
            } else if(this.pictureDriversID == "") {
                return msgError(401, "Drivers ID is required")
            } else if(this.pictureActualRS == "") {
                return msgError(401, "Actual RS is required")
            }

            formData.append('rs_id', this.rs_id)
            formData.append('rs_action', this.rs_action)
            formData.append('deliveryName', this.delivery_name)
            formData.append('plateNumber', this.plate_number)
            formData.append('user_id', this.$session.get('user_id'))
            formData.append('pictureInvoice', this.pictureInvoice)
            formData.append('pictureRSSlip', this.pictureRSSlip)
            formData.append('pictureDriversID', this.pictureDriversID)
            formData.append('pictureActualRS', this.pictureActualRS)
            formData.append('pictureInvoice_url', this.pictureInvoice_url)
            formData.append('pictureRSSlip_url', this.pictureRSSlip_url)
            formData.append('pictureDriversID_url', this.pictureDriversID_url)
            formData.append('pictureActualRS_url', this.pictureActualRS_url)
            
            try
            {
                let result = await axios.post(apiUrl+'purchaser/inquiry_rs/pickUpItem', formData, tokenApi())
                this.loading_status = true                
                let data = result.data
                this.status = data.status
                // this.isDisabled = false
                // this.isDisabled2 = false
                this.$router.push({ name: 'RsInquiry' })
            }catch (Exception) {
                this.handdleError(Exception)
            }
        },
        async getFormDetails() {
            let query = { 
                params: {
                    rs_id: this.$route.params.rs_id
                },
                headers: tokenApi('token', 'header')
            }
            if(this.$route.params.rs_action != '') {
                let result = await axios.get(apiUrl+'purchaser/inquiry_rs/viewItem', query)               
                if(result) {
                     if(result.data.picture == '') {
                        let data = result.data
                        // this.pictureImage = data.picture
                        this.delivery_name = data.deliveryName
                        this.plate_number = data.plateNumber
                    }
                    else
                    {
                        let data = result.data
                        // this.pictureImage = data.picture
                        this.delivery_name = data.deliveryName
                        this.plate_number = data.plateNumber
                        this.isReadonly = true
                        this.isDisabled2 = true
                    }
                }
            }
        }
    }
}

</script>


<style scoped>
input {
  border-radius: 0px !important;
   font-size: 10px !important;
   text-align:center ;
}
select{
    border-radius: 0px !important;
    text-align-last:center;
}
.btn {
    border-radius: 0px;
}
th,td {
  padding: 10px !important;
  font-size: 10px;
  text-align: center !important;
}
</style>

