<template>
    
   <div class="panel-body">
        <!-- Main content -->
        <div class="content-wrapper  col-sm-12">
            <div class="card">
                <div v-if="loading_status" class="alert alert-success">
                    <a href="#" class="close" data-dismiss="alert" aria-label="close">&times;</a>
                    <strong>Success!</strong> 
                    <br>
                    {{ status }}.
                </div>
                <form action="#" method="post" v-on:submit.prevent="submitForm()">
                    <h4 style="margin-top:0px;">Details</h4>
                    <div class="row">
                        <div class="col-md-3">
                            <div class="form-group">
                                <label><strong>Delivery Name:</strong></label>
                                <input type="text" :readonly="isReadonly" v-on:change="saveInfo($event, 'deliveryName')" class="form-control" placeholder="Delivery Name">
                            </div>
                        </div>
                        <div class="col-md-3">
                            <div class="form-group">
                                <label><strong>Plate Number:</strong></label>
                                <input type="text" :readonly="isReadonly" v-on:change="saveInfo($event, 'plateNumber')"  class="form-control" placeholder="Plate Number">
                            </div>
                        </div>
                        <div class="col-md-3">
                            <div class="form-group">
                                <label><strong>Invoice:</strong></label>
                                <div class="uniform-uploader">
                                        <input type="file" id="file" ref="invoice" accept="image/*" v-on:change="singleUpload($event, '', 'pictureInvoice')" class="form-input-styled form-control">
                                </div>
                            </div>

                            <!-- <div v-if="pictureInvoice != ''" class="form-group">
                                <label><strong>Invoice:</strong></label>
                                    <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureInvoice_url">
                            </div> -->
                        </div>
                        <div class="col-md-3">
                            <div class="form-group">
                                <label><strong>Driver's ID:</strong></label>
                                <div class="uniform-uploader">
                                        <input type="file" id="file" ref="driversID" accept="image/*" v-on:change="singleUpload($event, '', 'pictureDriversID')" class="form-input-styled form-control">
                                </div>
                            </div>
    
                            <!-- <div v-if="pictureDriversID != ''" class="form-group">
                                <label><strong>Driver's ID:</strong></label>
                                    <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureDriversID_url">
                            </div> -->
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6" v-for="(row, i) in rs_id" :key="i">
                            <h5 style="margin-top:0px;">{{ row }} </h5>
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <label><strong>Actual RS:</strong></label>
                                        <div class="uniform-uploader">
                                            <input type="file" id="file" ref="actualRS" accept="image/*" v-on:change="singleUpload($event, row, 'pictureActualRS')" class="form-input-styled form-control">
                                        </div>
                                    </div>
            
                                    <!-- <div v-if="pictureActualRS != ''" class="form-group">
                                        <label><strong>Actual RS:</strong></label>
                                            <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureActualRS[i].url">
                                    </div> -->
                                </div>
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <label><strong>RS Slip:</strong></label>
                                        <div class="uniform-uploader">
                                            <input type="file" id="file" ref="RSSlip" accept="image/*" v-on:change="singleUpload($event, row, 'pictureRSSlip')" class="form-input-styled form-control">
                                        </div>
                                    </div>
            
                                    <!-- <div v-if="pictureRSSlip != ''" class="form-group">
                                        <label><strong>RS Slip:</strong></label>
                                            <img class="img-responsive img-thumbnail" style="height:200px;" :src="pictureRSSlip_url">
                                    </div> -->
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="text-right">
                            <button v-on:click="redirectPrevious()" :disabled="isDisabled" type="button" class="btn btn-default" style="margin-right: 5px;">Cancel</button>
                            <button type="submit" class="btn btn-primary" @click="submitForm" :disabled="isDisabled2">Save</button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>


</template>
<script>

</script>
<script>
import axios from 'axios'
import moment from 'moment'
import { msgError, msgSuccess, tokenApi, apiUrl, imageUrl} from './../../assets/custom/custom.js'
import { async } from 'q'

export default {
    data (){
        return {
            rs_id: [],
            rs_action: '',
            delivery_name: '',
            plate_number: '',
            file: '',
            status: '',
            pictureImage: '',
            pictureInvoice: '',
            pictureRSSlip: [],
            pictureDriversID: '',
            pictureActualRS: [],
            loading_status: false,
            isDisabled: false,
            isDisabled2: false,
            isReadonly: false,
            formDatas: []
        }
    },
    created() {
        this.rs_id = JSON.parse(this.$route.params.rs_info)
        this.rs_action = this.$route.params.rs_action
    },
    computed: {
         message(){
             return this.$store.getters.message
         }
    },
    async mounted(){
        await this.createTempData()
        // await this.getFormDetails()
    },
    methods: {
        async saveInfo(event, description) {
            await axios.post(apiUrl+'purchaser/inquiry_rs/save_temp_data', 
            {
                rs_id: this.rs_id,
                value: event.target.value,
                description: description
            }
            , tokenApi())
        },
        async createTempData() {
            try {
                let data = {
                    rs_id: this.rs_id,
                    rs_action: this.rs_action
                }
                await axios.post(apiUrl+'purchaser/inquiry_rs/create_temp_data', data, tokenApi())
            } catch(e) {
                await this.handdleError(e)
            }
        },
        async singleUpload(event, row, description, type) {
            try {
                let file = event.target.files[0]
                let file_url = URL.createObjectURL(file)

                let formData = new FormData()
                
                if(description == 'pictureActualRS') {
                    formData.append('file', file)
                    formData.append('url', file_url)
                    formData.append('rs_id', row)
                    formData.append('rs_action', 1)
                    formData.append('description', description)
                    formData.append('user_id', this.$session.get('user_id'))
                    await axios.post(apiUrl+'purchaser/inquiry_rs/upload_temp_file_actualrs', formData, tokenApi())
                } else if(description == 'pictureRSSlip') {
                    formData.append('file', file)
                    formData.append('url', file_url)
                    formData.append('rs_id', row)
                    formData.append('rs_action', 1)
                    formData.append('description', description)
                    formData.append('user_id', this.$session.get('user_id'))
                    await axios.post(apiUrl+'purchaser/inquiry_rs/upload_temp_file_rsslip', formData, tokenApi())
                } else if (description == 'pictureDriversID') {
                    formData.append('file', file)
                    formData.append('url', file_url)
                    formData.append('rs_id', JSON.stringify(this.rs_id))
                    formData.append('rs_action', 1)
                    formData.append('description', description)
                    formData.append('user_id', this.$session.get('user_id'))
                    await axios.post(apiUrl+'purchaser/inquiry_rs/upload_temp_file_driversid', formData, tokenApi())
                } else if (description == 'pictureInvoice') {
                    formData.append('file', file)
                    formData.append('url', file_url)
                    formData.append('rs_id', JSON.stringify(this.rs_id))
                    formData.append('rs_action', 1)
                    formData.append('description', description)
                    formData.append('user_id', this.$session.get('user_id'))
                    await axios.post(apiUrl+'purchaser/inquiry_rs/upload_temp_file_invoice', formData, tokenApi())
                }
            } catch(e) {
                console.log(e)
            }
        },
        async handdleError(error=null) {
            if (error != null) {
                const response = JSON.parse(JSON.stringify(error))
                const status = response.response.status.toString()
                const message = (response.response.data.error === undefined) ? response.response.data.message : response.response.data.error.message
                msgError(status, message)
                return true
            }
                msgError(401, "Something Wrong")
        },
        async redirectPrevious() {
            this.$router.push({ name: 'RsInquiry' })
        },
        async invoiceFile() {
            this.pictureInvoice = this.$refs.invoice.files[0]
        },
        async rsslipFile(rsid, i) {
            this.pictureRSSlip.push({
                rs_id: rsid,
                file: this.$refs.RSSlip[i].files[0],
                url: URL.createObjectURL(this.$refs.RSSlip[i].files[0])
            })
        },
        async driveridFile() {
            this.pictureDriversID = this.$refs.driversID.files[0]
        },
        async actualrsFile(rsid, i) {
            this.pictureActualRS.push({
                rs_id: rsid,
                file: this.$refs.actualRS[i].files[0],
                url: URL.createObjectURL(this.$refs.actualRS[i].files[0])
            })
        },
        async submitForm() {
            this.isDisabled = true
            this.isDisabled2 = true
            // let formData = new FormData()
            // if (this.file == "") {
            //     return msgError(401, "Invoice file is required")
            // } else 
            // if(this.delivery_name == "") {
            //     return msgError(401, "Delivery name is required")
            // } else if(this.plate_number == "") {
            //     return msgError(401, "Plate number is required")
            // } else if(this.pictureInvoice == "") {
            //     return msgError(401, "Invoice is required")
            // } else if(this.pictureDriversID == "") {
            //     return msgError(401, "Drivers ID is required")
            // }

            // for(const row of this.pictureActualRS) {
            //     if(row.file == "") {
            //         return msgError(401, `Picture of Actual RS#${row.rs_id}} is required`)
            //     }
            // }
            // for(const row of this.this.RSSlip) {
            //     if(row.file == "") {
            //         return msgError(401, `Picture of RS Slip#${row.rs_id}} is required`)
            //     }
            // }
            // formData.append('rs_action', this.rs_action)
            // formData.append('deliveryName', this.delivery_name)
            // formData.append('plateNumber', this.plate_number)
            // formData.append('user_id', this.$session.get('user_id'))
            // formData.append('pictureInvoice', this.pictureInvoice)
            // formData.append('pictureRSSlip', this.pictureRSSlip)
            // formData.append('pictureDriversID', this.pictureDriversID)
            // formData.append('pictureActualRS', this.pictureActualRS)
            // formData.append('pictureInvoice_url', this.pictureInvoice_url)
            // formData.append('pictureRSSlip_url', this.pictureRSSlip_url)
            // formData.append('pictureDriversID_url', this.pictureDriversID_url)
            // formData.append('pictureActualRS_url', this.pictureActualRS_url)
            
            try
            {
                let dataForm = {
                    rs_id: this.rs_id,
                    rs_action: this.rs_action,
                    user_id: this.$session.get('user_id')
                }
                let result = await axios.post(apiUrl+'purchaser/inquiry_rs/pickUpItemBulk', dataForm, tokenApi())
                // let data = result.data
                // this.status = data.status
                this.status = 'You successfully updated RS ID: '
                for(let i = 0; i<result.data.result.length; i++) {
                    this.status += result.data.result[i]+' '
                }
                this.loading_status = true                
                this.isDisabled = false
                this.isDisabled2 = false
                // this.$router.push({ name: 'RsInquiry' })
            }catch (Exception) {
                await this.handdleError(Exception)
            }
        },
        async getFormDetails() {
            try {
                let query = { 
                    params: {
                        rs_id: this.$route.params.rs_id
                    },
                    headers: tokenApi('token', 'header')
                }
                if(this.$route.params.rs_action != '') {
                    let result = await axios.get(apiUrl+'purchaser/inquiry_rs/viewItem', query)               
                    if(result) {
                        if(result.data.picture == '') {
                            let data = result.data
                            // this.pictureImage = data.picture
                            this.delivery_name = data.deliveryName
                            this.plate_number = data.plateNumber
                        }
                        else
                        {
                            let data = result.data
                            // this.pictureImage = data.picture
                            this.delivery_name = data.deliveryName
                            this.plate_number = data.plateNumber
                            this.isReadonly = true
                            this.isDisabled2 = true
                        }
                    }
                }
            } catch(e) {
                console.log(e)
            }
        }
    }
}

</script>


<style scoped>
input {
  border-radius: 0px !important;
   font-size: 10px !important;
   text-align:center ;
}
select{
    border-radius: 0px !important;
    text-align-last:center;
}
